import Page from 'components/Page';
import React from 'react';
import classnames from 'classnames';
import {
  TabContent, 
  TabPane, 
  Nav, 
  NavItem, 
  NavLink, 
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
  Table,
} from 'reactstrap';
import states from "../api/states";
import PageSpinner from 'components/PageSpinner';
import { findLoad, editLoad, findDrivers, findTrucks, findDistance,findBrokers, findClients} from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import MUIDataTable from "mui-datatables";
class EditLoadPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id:this.props.match.params.id,
            activeTab:'1',
            date : '',
            bol : '',
            company : 'nn',
            contact:null,
            street:'',
            city:'',
            state:'nn',
            zip:'',
            broker:'',
            d_date : '',
            pol : '',
            d_company : 'nn',
            d_contact:null,
            d_street:'',
            d_city:'',
            d_state:'nn',
            d_zip:'',
            d_broker:'',
            truck: 'nn',
            trailer:'',
            miles:'0.00',
            weight:'',
            rate:'',
            driver_a:'nn',
            driver_b:'nn',
            xtoken:'',
            msg: '',
            error: '',
            downloadFile:true,
            preload:false,
            data: [],
            trucks:[],
            drivers:[],
            brokers:[],
            companies:[],
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findTrucks(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    trucks:response.data,
                });
            }).catch((respError) => {});
            findDrivers(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    drivers:response.data,
                });
            }).catch((respError) => {});

            findClients(obje_results.user.token).then((response) => {
              this.setState({
                  ...this.state,
                  companies:response.data,
              });
            }).catch((respError) => {});

            findBrokers(obje_results.user.token).then((response) => {
              this.setState({
                  ...this.state,
                  brokers:response.data,
              });
          }).catch((respError) => {});
          /** this item */
          findLoad(obje_results.user.token, this.props.match.params.id)
          .then((response) => {
            //   console.log('load', response)
            this.setState({
                ...this.state,
                date : response.data.date,
                bol : response.data.bol,
                company : response.data.company,
                contact:response.data.contact,
                street:response.data.street,
                city:response.data.city,
                state:response.data.state,
                zip:response.data.zip,
                broker:response.data.broker,
                d_date : response.data.d_date,
                pol : response.data.pol,
                d_company : response.data.d_company,
                d_contact:response.data.d_contact,
                d_street:response.data.d_street,
                d_city:response.data.d_city,
                d_state:response.data.d_state,
                d_zip:response.data.d_zip,
                d_broker:response.data.d_broker,
                truck: response.data.truck,
                trailer:response.data.trailer,
                miles:response.data.miles,
                weight:response.data.weight,
                rate:response.data.rate,
                driver_a:response.data.driver_a,
                driver_b:response.data.driver_b,
            });
          }).catch((respError) => {});
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    isValidEmailAddress = (email) => {
        if (/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
            return true;
        }
        return false;
    }
    toggleTab = tab => {
      if( this.state.activeTab !== tab)
      {
        this.setState({...this.state, activeTab: tab});
      }
    }
    firstUpper = (str) => {
      let string = str.toLowerCase();
      return string.charAt(0).toUpperCase() + string.slice(1);
    }
    handleDateInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, date: v,});
    }
    handleBolInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, bol: v,});
    }
    handleCompanyInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, company: v,});
    }
    handleContactInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, contact: v,});
    }
    handleStreetInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, street: v,});
    }
    handleCityInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, city: v,});
    }
    handleStateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, state: v,});
    }
    handleZipInput = (event) => {
      const v = event.target.value;
      if(v.length === 5 && this.state.d_zip.length === 5 )
      {
        findDistance(this.state.xtoken, v, this.state.d_zip).then((distance) => {
          this.setState({...this.state, miles: distance.data});
        })
        .catch((error) => console.log('error', error));
      }
      this.setState({...this.state, zip: v,});
    }
    handleBrokerInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, broker: v,});
    }
    handleDDateInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, d_date: v,});
    }
    handlePolInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, pol: v,});
    }
    handleDCompanyInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, d_company: v,});
    }
    handleDcontactInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, d_contact: v,});
    }
    handleDStreetInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, d_street: v,});
    }
    handleDCityInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, d_city: v,});
    }
    handleDStateInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, d_state: v,});
    }
    handleDzipInput = (event) => {
      const v = event.target.value;
      if(this.state.zip.length === 5 && v.length === 5 )
      {
        findDistance(this.state.xtoken, this.state.zip, v).then((distance) => {
          this.setState({...this.state, miles: distance.data});
        })
        .catch((error) => console.log('error', error));
      }
      this.setState({...this.state, d_zip: v,});
    }
    handleTruckInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, truck: v,});
    }
    handleTrailerInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, trailer: v,});
    }
    handleWeightInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, weight: v,});
    }
    handleRateInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, rate: v,});
    }
    handleADriverInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, driver_a: v,});
    }
    handleBDriverInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, driver_b: v,});
    }
    handleSaveChanges = () => {
        this.setState({ ...this.state, preload:true});
        editLoad(this.state.xtoken, this.state, this.props.match.params.id)
        .then((response) => {
            if( response.status === 200)
            {
                this.setState({
                  ...this.state,
                  msg: response.message,
                  data: response.data,
                  preload:false,
                  error: '',
                });
                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    preload:false,
                    msg: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
                preload:false,
            });
            console.log('error', error);
        });
    }
    render() {
      return (
        <Card>
        <CardBody>
        <Nav tabs>
        <NavItem>
          <NavLink
            className={classnames({ active: this.state.activeTab === '1' })}
            onClick={() => { this.toggleTab('1'); }}
          >
            Modify Load Entry {this.state.bol}
          </NavLink>
        </NavItem>
      </Nav>
      <TabContent activeTab={this.state.activeTab}>
          <TabPane tabId="1">
          {/* New entry */}
          <Row>
            <Col xl={12} lg={12} md={12}>
              <Row>
                <Col xl={12} lg={12} md={12}>
                  <Card className="borderless">
                      {this.state.preload && (
                        <CardHeader><PageSpinner/></CardHeader>
                      )}
                    {this.state.error.length > 0 && (
                        <Alert color="danger">{this.state.error}</Alert>
                    )}
                    {this.state.msg.length > 0 && (
                        <Alert color="success">{this.state.msg}</Alert>
                    )}
                  </Card>
                </Col>
              </Row>

              <div className="row">
                <div className="col-xl-6 col-lg-6 col-md-12">
                <Card className="noboder-left">
                  <CardBody>
                  <CardHeader>Initial Pickup</CardHeader>
                  <br></br>
                  <Row className="">
                    <Col xl={6} lg={12} md={12}>
                          <Form inline >
                            <FormGroup>
                              <Label className="load-label" for="exampleEmail">Date*</Label>
                              <Input onChange={this.handleDateInput}
                                type="date" 
                                value={this.state.date}
                                name="bookdate"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="exampleEmail">Company*</Label>
                              <Input onChange={this.handleCompanyInput} value={this.state.company} defaultValue={this.state.company} type="select" name="co">
                                <option value="nn" disabled={true}>Select from clients</option>
                                {
                                  this.state.companies.map((stt, idx) => (
                                    <option key={idx} value={stt.id}>{stt.company}</option>
                                  ))
                                }
                              </Input>
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Contact*</Label>
                              <Input onChange={this.handleContactInput}
                                type="text"
                                value={this.state.contact}
                                name="origin"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Street*</Label>
                              <Input onChange={this.handleStreetInput}
                                type="text"
                                value={this.state.street}
                                name="ghgg"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Truck*</Label>
                              <Input onChange={this.handleTruckInput} value={this.state.truck} defaultValue={this.state.truck} type="select" name="trck">
                                <option value="nn" disabled={true}>Select Truck</option>
                                {
                                  this.state.trucks.map((stt, idx) => (
                                    <option key={idx} value={stt.id}>{stt.number + "-" + stt.make}</option>
                                  ))
                                }
                              </Input>
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Trailer</Label>
                              <Input onChange={this.handleTrailerInput}
                                type="text"
                                value={this.state.trailer}
                                name="gbb"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Miles*</Label>
                              <Input readOnly
                                type="number"
                                value={this.state.miles}
                                name="origin"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Weight*</Label>
                              <Input onChange={this.handleWeightInput}
                                type="number"
                                value={this.state.weight}
                                name="nngh"
                                placeholder=""
                              />
                            </FormGroup>
                          </Form>
                    </Col> 
                    <Col xl={6} lg={12} md={12}>
                          <Form inline >
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">City*</Label>
                              <Input onChange={this.handleCityInput}
                                type="text"
                                value={this.state.city}
                                name="origin"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">State*</Label>
                              <Input onChange={this.handleStateInput} value={this.state.state} defaultValue={this.state.state} type="select" name="state">
                                <option value="nn" disabled={true}>Select state</option>
                                {
                                  states.data.map((stt, idx) => (
                                    <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                                  ))
                                }
                              </Input>
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Zip*</Label>
                              <Input onChange={this.handleZipInput}
                                type="number"
                                value={this.state.zip}
                                name="origin"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Broker*</Label>
                              <Input onChange={this.handleBrokerInput} value={this.state.broker} defaultValue={this.state.broker} type="select" name="brk">
                                <option value="nn" disabled={true}>Select Broker</option>
                                {
                                  this.state.brokers.map((stt, idx) => (
                                    <option key={idx} value={stt.id}>{stt.name}</option>
                                  ))
                                }
                              </Input>
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">BOL*</Label>
                              <Input onChange={this.handleBolInput}
                                type="text"
                                value={this.state.bol}
                                name="bol"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Rate*</Label>
                              <Input onChange={this.handleRateInput}
                                type="number"
                                value={this.state.rate}
                                name="origin"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="exampleEmail">Driver 1*</Label>
                              <Input onChange={this.handleADriverInput} value={this.state.driver_a} defaultValue={this.state.driver_a} type="select" name="brk">
                                <option value="nn" disabled={true}>Select Driver 1</option>
                                {
                                  this.state.drivers.map((stt, idx) => (
                                    <option key={idx} value={stt.id}>{stt.fname + " " + stt.lname}</option>
                                  ))
                                }
                              </Input>
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="exampleEmail">Driver 2*</Label>
                              <Input onChange={this.handleBDriverInput} value={this.state.driver_b} defaultValue={this.state.driver_b} type="select" name="brk">
                                <option value="null" disabled={false}>no second driver</option>
                                {
                                  this.state.drivers.map((stt, idx) => (
                                    <option key={idx} value={stt.id}>{stt.fname + " " + stt.lname}</option>
                                  ))
                                }
                              </Input>
                            </FormGroup>
                          </Form>
                    </Col>            
                  </Row>
                  </CardBody>
                  </Card>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12">
                <Card className="noboder-right">
                  <CardBody>
                  <CardHeader>Final Delivery</CardHeader>
                  <br></br>
                  <Row className="">
                    <Col xl={6} lg={12} md={12}>
                          <Form inline >
                            <FormGroup>
                              <Label className="load-label" for="exampleEmail">Date*</Label>
                              <Input onChange={this.handleDDateInput}
                                type="date" 
                                value={this.state.d_date}
                                name="bbbbg"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="exampleEmail">Company*</Label>
                              <Input onChange={this.handleDCompanyInput} value={this.state.d_company} defaultValue={this.state.d_company} type="select" name="trck">
                                <option value="nn" disabled={true}>Select from clients</option>
                                {
                                  this.state.companies.map((stt, idx) => (
                                    <option key={idx} value={stt.id}>{stt.company}</option>
                                  ))
                                }
                              </Input>
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Contact*</Label>
                              <Input onChange={this.handleDcontactInput}
                                type="text"
                                value={this.state.d_contact}
                                name="origin"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Street*</Label>
                              <Input onChange={this.handleDStreetInput}
                                type="text"
                                value={this.state.d_street}
                                name="str"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">City*</Label>
                              <Input onChange={this.handleDCityInput}
                                type="text"
                                value={this.state.d_city}
                                name="stg"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">State*</Label>
                              <Input onChange={this.handleDStateInput} value={this.state.d_state} defaultValue={this.state.d_state} type="select" name="state">
                                <option value="nn" disabled={true}>Select state</option>
                                {
                                  states.data.map((stt, idx) => (
                                    <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                                  ))
                                }
                              </Input>
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">Zip*</Label>
                              <Input onChange={this.handleDzipInput}
                                type="number"
                                value={this.state.d_zip}
                                name="bgb"
                                placeholder=""
                              />
                            </FormGroup>
                            <FormGroup>
                              <Label className="load-label" for="examplePassword">POL*</Label>
                              <Input onChange={this.handlePolInput}
                                type="text"
                                value={this.state.pol}
                                name="origin"
                                placeholder=""
                              />
                            </FormGroup>
                          </Form>
                    </Col> 
                    <Col xl={6} lg={12} md={12}>
                          <Form inline >
                            <Button onClick={this.handleSaveChanges}>Save changes</Button>
                          </Form>
                          <br></br>
                          <a href="/manage-loads">Back to loads</a>
                    </Col>            
                  </Row>
                  </CardBody>
                  </Card>
                </div>
              </div>
            </Col>
          </Row>
          {/* end new entry */}
        </TabPane>
      </TabContent>
        </CardBody>
      </Card>
        // <Page title={<a href="/manage-loads">Back to loads</a>} breadcrumbs={[{ name: '', active: false }]}>
            
        // </Page>
      );
    }
}
export default EditLoadPage;
